.container {
    max-width: 120rem;
    margin: 8rem auto;
    background-color: var(--color-grey-light-1);
    box-shadow: var(--shadow-dark);

    @media only screen and (max-width: $bp-1200px) {
        margin: 0;
        max-width: 100%;
        width: 100%;
    }
}
.content {
    display: flex;

    @media only screen and (max-width: $bp-900px) {
        flex-direction: column;
    }
}
.header {
    font-size: 1.4rem;
    height: 7rem;
    background-color: var(--color-off-white);
    border-bottom: var(--bottom-line);
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media only screen and (max-width: $bp-500px) {
        flex-wrap: wrap;
        align-content: space-around;
        height: 11rem;
    }
}
.sidebar {
    background-color: var(--color-grey-dark-1);
    flex: 0 0 18%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.hotel-view {
    background-color: rgb(241, 241, 241);
    flex: 1;
}
.detail {
    display: flex;
    padding: 4.5rem;
    background-image: linear-gradient(to right bottom, var(--color-grey-light-1), var(--color-grey-light-3));
    font-size: 1.4rem;

    @media only screen and (max-width: $bp-900px) {
        padding: 3rem;
    }
    @media only screen and (max-width: $bp-600px) {
        flex-direction: column;
    }
}
.description {
    flex: 0 0 60%;
    margin-right: 4.5rem;
    background-color: var(--color-off-white);
    box-shadow: var(--shadow-light);
    padding: 2rem;

    @media only screen and (max-width: $bp-900px) {
        padding: 1rem;
        margin-right: 3rem;
    }
    @media only screen and (max-width: $bp-600px) {
        margin-right: 0;
        margin-bottom: 3rem;
    }
}
.user-reviews {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}