// logo
.logo { 
    height: 3.25rem;
    margin-left: 2rem;
}
// search
.search {
    flex: 0 0 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-grey-light-2);
    padding: .7rem 2rem;
    border-radius: 10rem;

    @media only screen and (max-width: $bp-500px) {
        order: 1;
        flex: 0 0 100%;
        background-color: var(--color-grey-light-2);
        border-radius: 0;
    }
    &__input {
        font-family: inherit;
        font-size: inherit;
        background-color: var(--color-grey-light-2);
        color: inherit;
        width: 100%;
        transition: all .2s;
        border: none;
        
        &:focus {
            outline: none;
        }
        &::-webkit-input-placeholder {
            font-weight: 100;
            color: var(--color-grey-light-4);
        }
    }
    &__button {
        border: none;
        background-color: var(--color-grey-light-2);

        &:focus {
            outline: none;
        }
        &:active {
            transform: translateY(.2rem);
        }
    }
    &__icon {
        height: 2rem;
        width: 2rem;
        fill: var(--color-grey-dark-3);
    }
}
// user navigation
.user-nav {
    align-self: stretch;
    display: flex;
    align-items: center;

    & > * {
        padding: 0 2rem;
        cursor: pointer;
        height: 100%;
        display: flex;
        align-items: center;

        &:hover {
            background-color: var(--color-grey-light-2); 
        }
    }
    &__icon-box {
        position: relative;
    }
    &__icon {
        height: 2.25rem;
        width: 2.25rem;
        fill: var(--color-grey-dark-2);
    }
    &__notification {
        font-size: .85rem;
        height: 1.75rem;
        width: 1.75rem;
        border-radius: 50%;
        background-color: var(--color-primary);
        color: var(--color-off-white);
        position: absolute;
        top: 1.5rem;
        right: 1.1rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__user-photo {
        height: 3.75rem;
        width: 3.75rem;
        border-radius: 50%;
        margin-right: 1rem;
    }
}
// side navigation
.side-nav {
    font-size: 1.4rem;
    list-style: none;
    margin-top: 3.5rem;

    @media only screen and (max-width: $bp-900px) {
        display: flex;
        margin: 0;
    }

    &__item {
        position: relative;

        &:not(:last-child) {
            margin-bottom: .5rem;

            @media only screen and (max-width: $bp-900px) {
                margin-bottom: 0;
            }
        }
        @media only screen and (max-width: $bp-900px) {
            flex: 1;
        }
    }
    &__item::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: .3rem;
        background-color: var(--color-primary);
        transform: scaleY(0);

        transition: transform .2s, width .4s cubic-bezier(1,0,0,1) .2s, background-color .2s;
    }
    &__item:hover::before, &__item--active::before {
        transform: scaleY(1);
        width: 100%;
    }
    &__item:active::before {
        background-color: var(--color-primary-light);
    }
    &__link {
        &:link, &:visited {
            color: var(--color-grey-light-1);
            text-decoration: none;
            text-transform: uppercase;
            display: inline-block;
            padding: 1.5rem 3rem;
            display: flex;
            align-items: center;
            position: relative;
            z-index: 10;

            @media only screen and (max-width: $bp-900px) {
                justify-content: center;
                padding: 2rem;
            }
            @media only screen and (max-width: $bp-600px) {
                flex-direction: column;
                padding: 1rem;
            }
        }
    }
    &__icon {
        width: 1.75rem;
        height: 1.75rem;
        fill: currentColor;
        margin-right: 2rem;

        @media only screen and (max-width: $bp-600px) {
            margin-right: 0;
            margin-bottom: .7rem;
            width: 1.5rem;
            height: 1.5rem;
        }
    }
}
// legal text
.legal {
    font-size: 1.2rem;
    text-align: center;
    padding: 2.5rem;
    color: var(--color-grey-light-4);

    @media only screen and (max-width: $bp-900px) {
        display: none;
    }
}
// main gallery
.gallery {
    display: flex;

    // &__item {}
    &__photo {
        width: 100%;
        display: block;
    }
}
// main overview
.overview {
    display: flex;
    align-items: center;
    border-bottom: var(--bottom-line);

    &__heading {
        font-size: 2.25rem;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: .1rem;
        padding: 1.5rem 3rem;

        @media only screen and (max-width: $bp-600px) {
            font-size: 1.8rem;
            padding: 1.25rem 2rem;
        }
    }
    &__stars {
        margin-right: auto;
        display: flex;
        align-items: center;
    }
    &__icon--star {
        width: 1.75rem;
        height: 1.75rem;
        fill: var(--color-primary);
    }
    &__location {
        font-size: 1.2rem;
        display: flex;
        align-items: center;
    }
    &__icon--location {
        width: 1.75rem;
        height: 1.75rem;
        fill: var(--color-primary);
        margin-right: .5rem;
    }
    &__rating {
        background-color: var(--color-primary);
        margin-left: 3rem;
        color: var(--color-off-white);
        align-self: stretch;
        padding: 0 2.25rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        @media only screen and (max-width: $bp-600px) {
            padding: 0 1.5rem;
        }
        &--average {
            font-size: 2.25rem;
            margin-bottom: -.3rem;

            @media only screen and (max-width: $bp-600px) {
                font-size: 1.8rem;
            }
        }
        &--count {
            font-size: .8rem;
            text-transform: uppercase;

            @media only screen and (max-width: $bp-600px) {
                font-size: .5rem;
            }
        }
    }
}
// button
.btn-inline {
    border: none;
    color: var(--color-primary);
    font-size: inherit;
    border-bottom: .1rem solid currentColor;
    padding-bottom: .2rem;
    display: inline-block;
    background-color: transparent;
    cursor: pointer;
    transition: all .2s;

    span {
        margin-left: .3rem;
        transition: all .2s;
    }
    &:hover {
        transform: scale(1.1);
        margin-left: .8rem;

        span {
            margin-left: .8rem;
        }
    }
    &:focus {
        outline: none;
        animation: pulsate 1s infinite;
    }
}
// paragraph
.paragraph:not(:last-of-type) {
    margin-bottom: 2rem;
}
// list
.list {
    list-style: none;
    margin: 3rem 0;
    padding: 3rem 0;
    border-top: var(--line);
    border-bottom: var(--line);
    display: flex;
    flex-wrap: wrap;

    &__item {
        flex: 0 0 50%;
        margin-bottom: .7rem;

        &::before {
            content: "";
            display: inline-block;
            height: 1rem;
            width: 1rem;
            // background-image: url(../images/SVG/chevron-thin-right.svg);
            // background-size: cover;
            margin-right: .6rem;
            background-color: var(--color-primary);
            // -webkit-mask-image: url(../images/SVG/chevron-thin-right.svg);
            // -webkit-mask-size: cover;
            mask-image: url(../images/SVG/chevron-thin-right.svg);
            mask-size: cover;
        }
    }
}
// recommendation
.recommend {
    font-size: 1.3rem;
    color: var(--color-grey-dark-3);
    display: flex;
    align-items: center;

    &__count {
        margin-right: auto;
    }
    &__friends {
        display: flex;
    }
    &__photo {
        box-sizing: content-box;
        height: 4rem;
        width: 4rem;
        border-radius: 50%;
        border: .3rem solid white;

        &:not(:last-child) {
            margin-right: -2rem;
        }
    }
}
// review
.review {
    background-color: var(--color-off-white);
    box-shadow: var(--shadow-light);
    padding: 3rem;
    margin-bottom: 3.5rem;
    position: relative;
    overflow: hidden;

    @media only screen and (max-width: $bp-900px) {
        padding: 2rem;
        margin-bottom: 2rem;
    }

    &__text {
        margin-bottom: 2rem;
        position: relative;
        z-index: 10;
    }
    &__user {
        display: flex;
        align-items: center;
        font-size: 1.1rem;

        &-box {
            margin-right: auto;
        }
        &-name {
            font-weight: 600;
            text-transform: uppercase;
            margin-bottom: .4rem;
        }
    }
    &__photo {
        height: 5rem;
        width: 5rem;
        border-radius: 50%;
        margin-right: 1.5rem;
    }
    &__user-date {
        color: var(--color-grey-dark-3);
    }
    &__rating {
        font-size: 2.25rem;
        color: var(--color-primary);
    }
    &::before {
        content: "\201C";
        position: absolute;
        top: -11rem;
        left: -1.3rem;
        font-size: 25rem;
        font-family: sans-serif;
        color: var(--color-grey-light-2);
        z-index: 9;
    }
}
// call to action
.cta {
    padding: 3.5rem 0;
    text-align: center;

    @media only screen and (max-width: $bp-900px) {
        padding: 2.5rem 0;
    }

    &__book-now {
        font-size: 2rem;
        text-transform: uppercase;
        margin-bottom: 2.5rem;
    }
}
// call to action button
.btn {
    font-size: 1.5rem;
    border: none;
    text-transform: uppercase;
    border-radius: 10rem;
    background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
    color: var(--color-off-white);
    position: relative;
    overflow: hidden;
    cursor: pointer;

    & > * {
        display: inline-block;
        height: 100%;
        width: 100%;
        transition: all .2s;
    }
    &__visible {
        padding: 2rem 7.5rem;
    }
    &__invisible {
        padding: 2rem 0;
        position: absolute;
        left: 0;
        top: -100%;
    }
    &:hover {
        background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark));
    }
    &:hover &__visible {
        transform: translateY(100%);
    }
    &:hover &__invisible {
        top: 0;
    }
    &:focus {
        outline: none;
        animation: pulsate 1s infinite;
    }
}
// animation
@keyframes pulsate {
    0% {
        transform: scale(1);
        box-shadow: none;
    }
    50% {
        transform: scale(1.05);
        box-shadow: var(--shadow-light);
    }
    100% {
        transform: scale(1);
        box-shadow: none;
    }
}