
:root {
    // COLORS
    --color-primary: #eb2f64;
    --color-primary-light: #FF3366;
    --color-primary-dark: #BA265D;

    --color-grey-light-1: #faf9f9;
    --color-grey-light-2: #f4f2f2;
    --color-grey-light-3: #e7e5e5;
    --color-grey-light-4: #ccc;

    --color-grey-dark-1: #333;
    --color-grey-dark-2: #777;
    --color-grey-dark-3: #999;

    --color-off-white: #fefefe;
    // SHADOWS
    --shadow-dark: 0 2rem 6rem rgba($color: black, $alpha: .3);
    --shadow-light: 0 .1rem .1rem rgba($color: black, $alpha: .01);
    // BORDER
    --line: .1rem solid var(--color-grey-light-2);
}
// BREAKPOINTS
$bp-1200px: 75em;
$bp-1100px: 68.75em;
$bp-900px: 56.25em;
$bp-600px: 37.5em;
$bp-500px: 31.25em;

* {
    margin: 0;
    padding: 0;
}
html {
    box-sizing: border-box;
    font-size: 62.5%;

    @media only screen and (max-width: $bp-1100px) {
        font-size: 50%;
    }
}
*, *::before, *::after {
    box-sizing: inherit;
}
body {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
    line-height: 1.6;
    background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
    background-size: cover;
    min-height: 100vh;
    color: var(--color-grey-dark-2);
}